h1,
h2,
p,
ul {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  display: block;
}


ul,ol {
  list-style: none;
  display: block;
}
a {
  display: inline-block;
  text-decoration: none;
}
.logo {
  display: block;
}
.container {
  width: 100%;
  margin: 0 auto;

  @media screen and (min-width: 320px) {
    max-width: 320px;
  }
  @media screen and (min-width: 768px) {
    max-width: 768px;
  }
  @media screen and (min-width: 1444px) {

    max-width: 1444px;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}
